.images-wrapper{
  width: 85%;
  margin: 0 auto;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack:center;
      justify-content:center;
}
.rotate-content{
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
 -webkit-perspective: 600px;
         perspective: 600px;
  -webkit-transform-style: preserve-3d;
  -webkit-transform: translateZ(-245px) rotateY(45deg);
  transform-style: preserve-3d;
  transform: translateZ(-245px) rotateY(45deg) ;
}

.sing-image_wrapper{
  width: 240px;
  margin: 10px;
  position: relative;
}
.image{
  width: 100%;
}
.menu-wrapper{

}
.line{
  height: 2px;
  border-radius: 5px;
  background: #fff;
  margin: 10px;
  position: relative;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  -webkit-box-shadow: 0px 0px 8px 1px rgba(255,255,255,0.8);
          box-shadow: 0px 0px 8px 1px rgba(255,255,255,0.8);
}
.line1{
  width: 38px;
}
.line2{
  width: 25px;
  margin-left: 12px;
  background: red;
}
.line3{
  width: 33px;
}
.move-line:nth-child(1){
  opacity: 0;
}
.move-line:nth-child(2){
  -webkit-transform: rotateZ(45deg);
      -ms-transform: rotate(45deg);
          transform: rotateZ(45deg);
  width: 45px;
  right: 10px;
}
.move-line:nth-child(3){
  -webkit-transform: rotateZ(-50deg);
      -ms-transform: rotate(-50deg);
          transform: rotateZ(-50deg);
}
nav{
  width: 40%;
  right: 0;
  position: absolute;
  z-index: 99;
}
nav::after{
  content: '';
  display: block;
  clear: both;
}
.nav-links{
  text-align: center;
}
.nav-link{
  color: #fff;
  font-size: 44px;
  list-style: none;
  margin: 20px auto;
  cursor: pointer;
}
.nav-link:hover{
  color: #b9b9b9;
}
.menu{
  margin: 5px;
  position: absolute;
  right: 10px;
  top: 10px;
}
.menu-wrapper{
  margin-top: 90px;
}
.open{
  #transition:1s;
  -webkit-animation: openMenu 1s forwards;
          animation: openMenu 1s forwards;
}
.close{
  -webkit-transition:1s;
  -o-transition:1s;
  transition:1s;
  -webkit-animation: closeMenu 1s forwards;
          animation: closeMenu 1s forwards;
}
@-webkit-keyframes openMenu {
  from{display: none; margin-left: 100%}
    to{display: block;margin-left: 0%}
}
@keyframes openMenu {
  from{display: none; margin-left: 100%}
    to{display: block;margin-left: 0%}
}
@-webkit-keyframes closeMenu {
  from{display: block; margin-left: 0%}
    to{display: none;margin-left: 100%}
}
@keyframes closeMenu {
  from{display: block; margin-left: 0%}
    to{display: none;margin-left: 100%}
}
@media screen and (max-width:800px) {
  .rotate-content {
    -webkit-transform: translateZ(-80px) rotateY(25deg);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    transform: translateZ(-80px) rotateY(25deg);
  }
}
@media screen and (max-width:535px) {
  nav{
    width: 100%;
  }
  .rotate-content {
    -webkit-transform: translateZ(0px) rotateY(-15deg);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
        -webkit-transform: translateY(0px) rotateZ(-15deg);
            -ms-transform: translateY(0px) rotate(-15deg);
                transform: translateY(0px) rotateZ(-15deg)
  }
}



